import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { NotifydetailsComponent } from '../notifydetails/notifydetails.component';

@Component({
  selector: 'app-listnotify',
  templateUrl: './listnotify.page.html',
  styleUrls: ['./listnotify.page.scss'],
})
export class ListnotifyPage implements OnInit {

  constructor(private modalController: ModalController) { }

  ngOnInit() {
  }

  async showDetail() {
    const modal = await this.modalController.create({
      component: NotifydetailsComponent,
      cssClass: 'fullScreenModal'
    });
    return await modal.present();
  }
}

